


.container {
  display: flex;
  margin-top: 50px;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.form {
  background-color: #fff;
  padding: 2rem;
  border-radius: 0.5rem;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  width: 400px;
}

.title {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
}

.formGroup {
  margin-bottom: 1rem;
}

.label {
  display: block;
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

.input {
  display: block;
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
  font-size: 1rem;
}

.button {
  display: block;
  width: 100%;
  background-color: #0077cc;
  color: #fff;
  border: none;
  padding: 0.5rem;
  border-radius: 0.25rem;
  font-size: 1rem;
  margin-top: 1rem;
}